import { Component, OnInit, Injector, Input, ViewEncapsulation, ViewChild, ElementRef, TemplateRef, ViewContainerRef, Output, EventEmitter } from '@angular/core';

import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { MatButton } from '@angular/material/button';

import { TemplatePortal } from '@angular/cdk/portal';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';


@Component({
  selector: 'app-add-gl-account',
  templateUrl: './add-gl-account.component.html',
  styleUrls: ['./add-gl-account.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class AddGlAccountComponent implements OnInit {

  gl:any;
  glGroupsDataList:any;
  glDefaultBankDataList:any;

  isAccountAdded:boolean = true;
  
  isError:boolean = false;
  errorMessage:string = "";

  modalService:any;

  @Input() type:any;
  @Input() group:any;
  @Input() glaccountTypeId:any;
  @Input() glAccountId?:any;

  // Private
  private _selectPanelOverlayRef: OverlayRef;

  @ViewChild('glAccountPanel')
  private _glAccountPanel: TemplateRef<any>;

  @ViewChild('addAccountElem')
  private _addAccountElem: MatButton;

  constructor(
    private _overlay: Overlay,
    private _viewContainerRef: ViewContainerRef,
    private injector: Injector,
    private accountsService:AccountsService,
  	private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  getGlGroup(event) {
    this.gl.glgroupId = event[0].glgroupId
  }

  openGlAccount() {

    // Create the overlay
    this._selectPanelOverlayRef = this._overlay.create({
      backdropClass   : '',
      hasBackdrop     : true,
      scrollStrategy  : this._overlay.scrollStrategies.block(),
      positionStrategy: this._overlay.position()
                            .flexibleConnectedTo(this._addAccountElem._elementRef.nativeElement)
                            .withFlexibleDimensions()
                            .withViewportMargin(56)
                            .withLockedPosition(false)
                            .withPositions([
                                {
                                    originX : 'start',
                                    originY : 'bottom',
                                    overlayX: 'start',
                                    overlayY: 'top'
                                }
                            ])
    });

    // Subscribe to the attachments observable
    this._selectPanelOverlayRef.attachments().subscribe(() => {

      // Focus to the search input once the overlay has been attached
      this._selectPanelOverlayRef.overlayElement.querySelector('select').focus();
  });

  // Create a portal from the template
  const templatePortal = new TemplatePortal(this._glAccountPanel, this._viewContainerRef);

  // Attach the portal to the overlay
  this._selectPanelOverlayRef.attach(templatePortal);

  // Subscribe to the backdrop click
  this._selectPanelOverlayRef.backdropClick().subscribe(() => {

      // If overlay exists and attached...
      if ( this._selectPanelOverlayRef && this._selectPanelOverlayRef.hasAttached() )
      {
          // Detach it
          this._selectPanelOverlayRef.detach();

      }

      // If template portal exists and attached...
      if ( templatePortal && templatePortal.isAttached )
      {
          // Detach it
          templatePortal.detach();
      }
  });

  }

  isCreateGroup(){
    return this.type == "add";
  }

  isAsset(){
    return this.group == 'Asset';
  }

  isLiability(){
    return this.group == 'Liability';
  }

  isIncome(){
    return this.group == 'Income';
  }

  isExpenses(){
    return this.group == 'Expenses';
  }

  submitGLAccountForm(form){

    this.isAccountAdded = false;

    if(this.isCreateGroup()){

      let details = {

        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "glgroupId": parseInt(this.gl.glgroupId),
        "glaccountNumber": this.gl.glaccountNumber,
        "glaccountName": this.gl.glaccountName,
        "openingBalance":  this.gl.openingBalance,
        "openingBalanceDate": this.gl.openingBalanceDate,
        "defaultBankDetailsId": null,
        "notes": this.gl.notes,
        "indicator": this.group,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": null,
        "updatedOn": null

      }

      let params = {
        glAccount: details
      }

      this.accountsService.addGlAccount(params).subscribe((res:any) => {

        this.isAccountAdded = true;

        if(res.message) {
          this.sharedService.setAlertMessage("Account added successfully");
          this.sharedService.setGlAccountAdded(true);
        }
        else {
          this.modalService.showErrorModal(res.errorMessage);
          this.sharedService.setGlAccountAdded(false);
        }


      })

    }
    else {

      let details = {

        "glaccountId": parseInt(this.gl.glaccountId),
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "glgroupId": parseInt(this.gl.glgroupId),
        "glaccountNumber": this.gl.glaccountNumber,
        "glaccountName": this.gl.glaccountName,
        "openingBalance":  this.gl.openingBalance,
        "openingBalanceDate": this.gl.openingBalanceDate,
        "defaultBankDetailsId": null,
        "notes": this.gl.notes,
        "indicator": this.group,
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString()

      }

      let params = {
        glAccount: details
      }

      this.accountsService.updateGlAccount(params).subscribe((res:any) => {

        this.isAccountAdded = true;

        if(res.message) {
           this.sharedService.setAlertMessage("Account updated successfully");
           this.sharedService.setGlAccountAdded(true);
        }
        else {
            this.isError = true;
            this.errorMessage = res.errorMessage;
            this.sharedService.setGlAccountAdded(false);
        }


      })

    }
  	

  }

  ngOnInit() {

  	this.gl = {};
  	this.gl.glgroupId = "";
    this.gl.defaultBankDetailsId = "";
    this.sharedService.setGlAccountAdded(false);

    if(this.group == 'Income' || this.group == 'Expenses'){
      this.gl.openingBalance = 0;
    }

    if(!this.isCreateGroup()){

       var params = {
          apartmentId: parseInt(this.cookieService.get('apartmentId')),
          glAccountId: this.glAccountId,
       }

      this.accountsService.getGlAccountsId(params).subscribe((res:any) => {

        if(res[0] != undefined)
        this.gl = res[0];


        if(this.group == 'Income' || this.group == 'Expenses'){
          this.gl.openingBalance = 0;
        }

      })
    };

    this.accountsService.getAllGlGroups().subscribe((res:any) => {
      	

      this.glGroupsDataList = res.filter(item => {
        return item.isActive  && item.glaccountTypeId == parseInt(this.glaccountTypeId)
      });
      
    });

    let defaultParams = {
      LookupTypeId: 60
    }

    //bank details
    this.lookupService.getLookupValueByLookupTypeId(defaultParams).subscribe((res:any) => {
          
       this.glDefaultBankDataList = res;


    },
    error => {
      
    }
    );


   
  }

}
