import { Component, OnInit, Injector, Input, ViewEncapsulation, ViewChild, ElementRef, TemplateRef, ViewContainerRef, Output, EventEmitter } from '@angular/core';

import { Overlay, OverlayRef } from '@angular/cdk/overlay';
import { MatButton } from '@angular/material/button';

import { TemplatePortal } from '@angular/cdk/portal';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { ModalService } from 'src/app/shared/services/modal.service';

@Component({
  selector: 'app-add-gl-group',
  templateUrl: './add-gl-group.component.html',
  styleUrls: ['./add-gl-group.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class AddGlGroupComponent implements OnInit {
 

  // Private
  private _selectPanelOverlayRef: OverlayRef;

  @ViewChild('glGroupPanel')
  private _glGroupPanel: TemplateRef<any>;

  @ViewChild('addGroupElem')
  private _addGroupElem: MatButton;


  gl:any;

  isGroupAdded:boolean = true;

  @Input() type:any;
  @Input() accountTypeId:any;
  @Input() group:any;
  @Input() glgroupId?:any;

  modalService:any;

  constructor(
    private _overlay: Overlay,
    private _viewContainerRef: ViewContainerRef,
    private injector: Injector,
    private accountsService:AccountsService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) {
    this.modalService = this.injector.get(ModalService);
   }

   openGlGroup() {

    // Create the overlay
    this._selectPanelOverlayRef = this._overlay.create({
      backdropClass   : '',
      hasBackdrop     : true,
      scrollStrategy  : this._overlay.scrollStrategies.block(),
      positionStrategy: this._overlay.position()
                            .flexibleConnectedTo(this._addGroupElem._elementRef.nativeElement)
                            .withFlexibleDimensions()
                            .withViewportMargin(56)
                            .withLockedPosition(false)
                            .withPositions([
                                {
                                    originX : 'start',
                                    originY : 'bottom',
                                    overlayX: 'start',
                                    overlayY: 'top'
                                }
                            ])
    });

    // Subscribe to the attachments observable
    this._selectPanelOverlayRef.attachments().subscribe(() => {

      // Focus to the search input once the overlay has been attached
      this._selectPanelOverlayRef.overlayElement.querySelector('input').focus();
  });

  // Create a portal from the template
  const templatePortal = new TemplatePortal(this._glGroupPanel, this._viewContainerRef);

  // Attach the portal to the overlay
  this._selectPanelOverlayRef.attach(templatePortal);

  // Subscribe to the backdrop click
  this._selectPanelOverlayRef.backdropClick().subscribe(() => {

      // If overlay exists and attached...
      if ( this._selectPanelOverlayRef && this._selectPanelOverlayRef.hasAttached() )
      {
          // Detach it
          this._selectPanelOverlayRef.detach();

      }

      // If template portal exists and attached...
      if ( templatePortal && templatePortal.isAttached )
      {
          // Detach it
          templatePortal.detach();
      }
  });

  }

  isCreateGroup(){
    return this.type == "add";
  }

  submitGLGroupForm(form){

    this.isGroupAdded = false;

    if(this.isCreateGroup()){

      let details = {

        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "glnumber": parseInt(this.gl.glnumber),
        "name": this.gl.name,
        "glaccountTypeId": this.accountTypeId, //assets
        "isActive": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "insertedOn": new Date().toISOString(),
        "updatedBy": 0,
        "updatedOn": new Date().toISOString()

      }

      let params = {
        glGroup: details
      }

      this.accountsService.addGlGroup(params).subscribe((res:any) => {

        this.isGroupAdded = true;

        if(res.message) {
          this.sharedService.setAlertMessage("Group added successfully");
          this.sharedService.setGlGroupAdded({id:this.accountTypeId, status: true});
        }
        else {
          this.modalService.showErrorModal(res.errorMessage);
          this.sharedService.setGlGroupAdded({id:this.accountTypeId, status: false});
        }


      })

    }
    else {

      let details = {

        "glgroupId": parseInt(this.glgroupId),
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "glnumber": parseInt(this.gl.glnumber),
        "name": this.gl.name,
        "glaccountTypeId": this.accountTypeId, //assets
        "isActive": true,
        "insertedBy": 0,
        "insertedOn": new Date().toISOString(),
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "updatedOn": new Date().toISOString()

      }

      let params = {
        glGroup: details
      }

      this.accountsService.updateGlGroup(params).subscribe((res:any) => {

        this.isGroupAdded = true;

        if(res.message) {
           this.sharedService.setAlertMessage("Group updated successfully");
           this.sharedService.setGlGroupAdded({id:this.accountTypeId, status: true});
        }
        else {
            this.modalService.showErrorModal(res.errorMessage);
            this.sharedService.setGlGroupAdded({id:this.accountTypeId, status: false});
        }


      })

    }
  	

  }
  

  ngOnInit() {

    this.gl = {};
    this.gl.glnumber = "";
    this.gl.name = "";

    this.sharedService.setGlGroupAdded({id:this.accountTypeId, status: false});
   
  }

  ngOnChanges() {

    this.gl = {};
    this.gl.glnumber = "";
    this.gl.name = "";

    if(!this.isCreateGroup()){

      var params = {
         apartmentId: parseInt(this.cookieService.get('apartmentId')),
         glGroupId: this.glgroupId,
      }

     this.accountsService.getGlGroupById(params).subscribe((res:any) => {

       /*this.gl = res[0];

       if(this.gl == undefined || this.gl.length == 0){
        this.gl.glnumber = "";
        this.gl.name = "";
       }*/


     })
   }

  }

}
